.contact-form-content{
    width: 100%;
    backdrop-filter: blur(1rem);
}
form{
    display: flex;
    flex-direction: column;
    grid-gap:1.5rem;
}
.name-container{
  display: flex;
  align-items: center;
  grid-gap: 1.5rem;
}
form input,
form textarea {
  flex: 1;
  width: 100%;
  font-size: 0.9rem;
  color:#fff;
  background: #130f2a;
  border-radius: 0.6rem;
  border: 1.5px solid#6751b9;
  padding: 1rem;
}
form button{
  font-size: 1rem;
  font-weight: 500;
  color: #fff;
  border: 1.5px solid #a892fe;
  border-radius: 0.6rem;
  background: linear-gradient(98deg,#a892fe 0%, #8064e8 100%);
    padding: 0.7rem;
    transition: all 0.3s ease;
    cursor: pointer;
}
form button:hover{
    color:#a892fe;
    background: transparent;
}
.paragraph{
    font-size: 0.8rem;
    text-align: center;
    margin-bottom: 3rem;
    line-height: 1.5rem;
}
.contact-form-content .heading{
    margin-bottom: 3rem;
}

@media (max-width: 450px){
   .name-container {
     flex-direction: column;
     align-items: flex-start;
   }
   form input,
   form textarea{
       padding: 0.7rem;
       border-radius: 0.3rem;
   }
   form button{
       font-size: 0.9rem;
       padding: 0.7rem;
       border-radius: 0.3rem;

   }
   .paragraph{
        font-size: 0.7rem;
        margin-bottom: 2rem;
        line-height: 1.2rem;
    }
}
